// App.js
import React, { useState } from 'react';
import { Container, Typography, Tabs, Tab, Paper } from '@mui/material';
import PadFoundationCalculator from './components/PadFoundationCalculator';
import StripFootingCalculator from './components/StripFootingCalculator';

function App() {
  // State to manage selected tab
  const [selectedTab, setSelectedTab] = useState(0);

  const handleTabChange = (event, newValue) => {
    setSelectedTab(newValue);
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4 }}>
      <Typography variant="h3" align="center" gutterBottom style={{ fontWeight: 'bold' }}>
        Foundation Calculator
      </Typography>

      {/* Tabs for switching between calculators */}
      <Paper elevation={3} sx={{ p: 2, borderRadius: '1rem' }}>
        <Tabs value={selectedTab} onChange={handleTabChange} aria-label="calculator tabs" textColor="primary"
          indicatorColor="transparent" sx={{ background: '#eee', borderRadius: '.5rem', width: '31%', p: .5, '& .MuiTab-root': {
              '&.Mui-selected': {color: 'black', background: 'white', borderRadius: '.5rem'}}}}>
          <Tab label="Pad Foundation" sx={{ textTransform: 'capitalize' }} />
          <Tab label="Strip Footing" sx={{ textTransform: 'capitalize' }} />
        </Tabs>
  

        {/* Conditional rendering based on the selected tab */}
        {selectedTab === 0 && <PadFoundationCalculator />}
        {selectedTab === 1 && <StripFootingCalculator />}
      </Paper>
    </Container>
  );
}

export default App;