// components/StripFootingCalculator.js
import React, { useState } from 'react';
import { Box, TextField, Typography, Button, Paper } from '@mui/material';

function StripFootingCalculator() {
  const [wallLoad, setWallLoad] = useState('');
  const [footingDimensions, setFootingDimensions] = useState(null);
  
  const handleCalculate = () => {
    if (!wallLoad) {
      alert('Please enter the Wall Load.');
      return;
    }

    // Example calculation logic
    const width = wallLoad * 0.1; // Example formula for width
    const depth = width * 0.5; // Example formula for depth
    const volume = width * depth * 1.5; // Example volume calculation

    setFootingDimensions({ width, depth, volume });
  };

  return (
    <Paper elevation={3} sx={{ boxShadow: 'none' }}>
      <Typography variant="h5" sx={{ my: '1rem' }}>
        Loading
      </Typography>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 2 }}>
        <TextField
          label="Wall Load (kN/m)"
          variant="outlined"
          value={wallLoad}
          onChange={(e) => setWallLoad(e.target.value)}
          fullWidth
        />
        <Button variant="contained" color="primary" onClick={handleCalculate} sx={{backgroundColor: '#333', color: 'white', width: '20%', fontWeight: 'bold', padding: '10px 20px', borderRadius: '.5rem', '&:hover': {backgroundColor: 'white', color: '#333'}}}>
          Calculate
        </Button>
      </Box>
      {footingDimensions && (
        <Box sx={{ mt: 4 }}>
          <Typography variant="h6">Results:</Typography>
          <Typography>Footing Width: {footingDimensions.width.toFixed(2)} m</Typography>
          <Typography>Footing Depth: {footingDimensions.depth.toFixed(2)} m</Typography>
          <Typography>Concrete Volume per Meter: {footingDimensions.volume.toFixed(2)} m³</Typography>
        </Box>
      )}
    </Paper>
  );
}

export default StripFootingCalculator;
