// components/PadFoundationCalculator.js
import React, { useState } from "react";
import { TextField, Button, Typography, Box, Paper } from "@mui/material";

function PadFoundationCalculator() {
  // State variables for inputs
  const [deadLoad, setDeadLoad] = useState("");
  const [liveLoad, setLiveLoad] = useState("");
  const [windLoad, setWindLoad] = useState("");
  const [soilPressure, setSoilPressure] = useState("");
  const [padThickness, setPadThickness] = useState(500); // Default to 500 mm
  const [concreteStrength, setConcreteStrength] = useState(32); // Default to 32 MPa
  const [columnWidth, setColumnWidth] = useState(500); // Column width in mm
  const [barDiameter, setBarDiameter] = useState(16); // Reinforcement bar diameter

  // State variables for calculated outputs
  const [totalLoad, setTotalLoad] = useState(null);
  const [padArea, setPadArea] = useState(null);
  const [padDimension, setPadDimension] = useState(null);
  const [concreteVolume, setConcreteVolume] = useState(null);
  const [shearCheckOneWay, setShearCheckOneWay] = useState(null);
  const [shearCheckTwoWay, setShearCheckTwoWay] = useState(null);

  // Handler for calculation
  const calculatePadFoundation = () => {
    // Parse input values
    const DL = parseFloat(deadLoad) || 0;
    const LL = parseFloat(liveLoad) || 0;
    const WL = parseFloat(windLoad) || 0;
    const σall = parseFloat(soilPressure) || 0;
    const f_c = parseFloat(concreteStrength);
    const columnWidthMeters = parseFloat(columnWidth) / 1000; // convert to meters
    const thicknessMeters = parseFloat(padThickness) / 1000; // convert to meters
    const barDiam = parseFloat(barDiameter);

    // Step 1: Calculate ULS Load Combination
    const loadCombo1 = 1.35 * DL;
    const loadCombo2 = 1.2 * DL + 1.5 * LL + 0.5 * WL;
    const factoredLoad = Math.max(loadCombo1, loadCombo2);

    // Step 2: Calculate Required Pad Area (m²)
    const area = factoredLoad / σall; // in square meters
    const dimension = Math.sqrt(area); // for a square pad

    // Step 3: Calculate Concrete Volume
    const volume = area * thicknessMeters; // in cubic meters

    // Step 4: Shear Checks
    const d_o = padThickness - 40 - barDiam / 2; // effective depth in mm

    // One-Way Shear
    const β3 = 1.0;
    const b_v = dimension * 1000; // convert to mm for consistency
    const Vuc_oneWay = β3 * b_v * d_o * Math.sqrt(f_c); // in N
    const shearForceOneWay = (factoredLoad * 1000 / (dimension * dimension)) * (dimension / 2) * b_v; // in N

    // Two-Way (Punching) Shear
    const u = 2 * (columnWidth + 2 * d_o); // critical perimeter in mm
    const Vuc_twoWay = u * d_o * Math.sqrt(f_c); // in N
    const appliedShearForceTwoWay = (factoredLoad * 1000) - (σall * 1000 * (columnWidthMeters ** 2)); // in N

    // Update state with calculated values
    setTotalLoad(factoredLoad.toFixed(2));
    setPadArea(area.toFixed(2));
    setPadDimension(dimension.toFixed(2));
    setConcreteVolume(volume.toFixed(2));
    setShearCheckOneWay(Vuc_oneWay >= shearForceOneWay ? "OK" : "Not OK");
    setShearCheckTwoWay(Vuc_twoWay >= appliedShearForceTwoWay ? "OK" : "Not OK");
  };

  return (
    


    <Paper elevation={3} sx={{ boxShadow: 'none' }}>
      <Typography variant="h5" sx={{ my: '1rem' }}>
        Pad Foundation Calculator
      </Typography>
      <TextField 
        label="Dead Load (kN)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={deadLoad}
        onChange={(e) => setDeadLoad(e.target.value)}
      />
      <TextField
        label="Live Load (kN)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={liveLoad}
        onChange={(e) => setLiveLoad(e.target.value)}
      />
      <TextField
        label="Wind Load (kN)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={windLoad}
        onChange={(e) => setWindLoad(e.target.value)}
      />
      <TextField
        label="Allowable Soil Pressure (kPa)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={soilPressure}
        onChange={(e) => setSoilPressure(e.target.value)}
      />
      <TextField
        label="Concrete Strength (MPa)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={concreteStrength}
        onChange={(e) => setConcreteStrength(e.target.value)}
      />
      <TextField
        label="Column Width (mm)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={columnWidth}
        onChange={(e) => setColumnWidth(e.target.value)}
      />
      <TextField
        label="Pad Thickness (mm)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={padThickness}
        onChange={(e) => setPadThickness(e.target.value)}
      />
      <TextField
        label="Reinforcement Bar Diameter (mm)"
        variant="outlined"
        fullWidth
        margin="normal"
        value={barDiameter}
        onChange={(e) => setBarDiameter(e.target.value)}
      />
      <Button
        variant="contained"
        color="primary"
        onClick={calculatePadFoundation}
        sx={{ mt: 2 }}
      >
        Calculate
      </Button>

      {totalLoad && (
        <Box mt={3}>
          <Typography variant="h6">Calculation Results:</Typography>
          <Typography>Total Load (ULS): {totalLoad} kN</Typography>
          <Typography>Required Pad Area: {padArea} m²</Typography>
          <Typography>
            Suggested Pad Dimension (Square): {padDimension} m x {padDimension} m
          </Typography>
          <Typography>Concrete Volume: {concreteVolume} m³</Typography>
          <Typography>One-Way Shear Check: {shearCheckOneWay}</Typography>
          <Typography>Two-Way Shear Check: {shearCheckTwoWay}</Typography>
        </Box>
      )}
    </Paper>
  );
}

export default PadFoundationCalculator;